* {
  font-family: 'Museo Sans Cyrl', sans-serif;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
.container {
  position: relative;
  width: 1140px;
  margin: 0 auto;
}
.promo {
  background: url("../../img/header_bg.jpg") center center / cover no-repeat;
  padding: 21px 0 93px;
}
.promo .container {
  padding: 0 100px;
}
.promo__cap {
  display: flex;
  justify-content: space-between;
}
.promo__item {
  font-size: 18px;
  font-weight: 700;
  text-align: center;
  color: #ffffff;
}
.promo__item span {
  font-size: 12px;
  font-weight: 500;
}
.promo__item:nth-child(3) {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.promo__header {
  margin-top: 91px;
  font-size: 42px;
  font-weight: 700;
  text-align: right;
  line-height: 0.9;
  color: #ffffff;
  text-transform: uppercase;
}
.promo__subheader {
  margin-top: 17px;
  font-size: 24px;
  font-weight: 500;
  text-align: right;
  line-height: 0.9;
  color: #ffffff;
  text-transform: uppercase;
}
.promo__flex {
  margin: 66px 19px 0 0;
  display: flex;
  justify-content: flex-end;
}
.promo__img {
  display: flex;
}
.promo__icons {
  width: 108px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 14px;
  font-weight: 500;
  text-align: center;
  color: #ffffff;
}
.promo__icons img {
  width: 45px;
  height: 45px;
}
.promo__link {
  width: 200px;
  margin-top: 18px;
  margin-left: auto;
  font-size: 16px;
  font-weight: 500;
  color: #ffffff;
}
.promo__link a {
  font-size: 16px;
  font-weight: 500;
  color: #ffffff;
  text-transform: uppercase;
}
.btn {
  display: block;
  width: 150px;
  height: 30px;
  border-radius: 5px;
  background-color: #c70101;
  font-size: 12px;
  font-weight: 500;
  text-align: center;
  color: #ffffff;
  border: none;
  text-transform: uppercase;
}
.btn_promo {
  width: 300px;
  height: 50px;
  font-size: 16px;
  margin-top: 67px;
  margin-left: auto;
}
.btn_consultation {
  width: 300px;
  height: 55px;
  font-size: 16px;
  margin-top: 5px;
}
.preference {
  padding: 70px 0 95px;
}
.preference__title {
  font-size: 30px;
  font-weight: 700;
  text-align: center;
  color: #0d0d0d;
}
.preference__wrapper {
  margin: 65px auto 0;
  display: flex;
  justify-content: space-between;
}
.preference__item {
  width: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 14px;
  font-weight: 300;
  text-align: center;
  color: #0d0d0d;
}
.preference__item img {
  width: 70px;
}
.preference__item h3 {
  font-size: 18px;
  font-weight: 700;
  color: #0d0d0d;
}
.preference__item span {
  color: #ff0000;
  text-transform: uppercase;
}
.consultation {
  padding: 100px 0 92px;
  background: url("../../img/consultation_bg.jpg") center center / cover no-repeat;
}
.consultation .container {
  padding-left: 102px;
}
.consultation__title {
  font-size: 36px;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
}
.consultation__description {
  margin-top: 80px;
  font-size: 20px;
  font-weight: 500;
  color: #ffffff;
}
.consultation__form {
  margin-top: 36px;
  width: 300px;
}
.consultation__form input {
  width: 100%;
  height: 50px;
  margin-bottom: 15px;
  border-radius: 5px;
  padding-left: 20px;
  background-color: #ffffff;
  font-size: 14px;
  font-weight: 300;
  color: #4b4b4b;
}
.carousel {
  padding: 81px 0;
}
.carousel__inner {
  position: relative;
  width: 750px;
  margin: 0 auto;
}
.carousel__inner img {
  height: 527px;
}
.carousel .tns-item img {
  display: block;
  margin: 0 auto;
}
.carousel .prev,
.carousel .next {
  background-color: rgba(0, 0, 0, 0);
  width: 31px;
  height: 50px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 66px;
  border: none;
}
.carousel .next {
  left: auto;
  right: 66px;
}
@media (max-width: 1200px) {
  .container {
    width: 100%;
  }
  .carousel .prev {
    top: auto;
    bottom: -70px;
    left: 40%;
  }
  .carousel .next {
    top: auto;
    bottom: -70px;
    right: 40%;
  }
}
@media (max-width: 991px) {
  .promo .container {
    padding: 0 10px;
  }
  .preference__wrapper {
    padding: 0 10px;
  }
  .consultation .container {
    padding-left: 20px;
  }
  .consultation__title {
    font-size: 30px;
  }
  .carousel .tns-item {
    width: 750px;
  }
  .carousel__inner img {
    height: auto;
  }
}
@media (max-width: 767px) {
  .promo__cap {
    align-items: center;
  }
  .promo__item img {
    width: 30vw;
  }
  .preference__item {
    font-size: 10px;
  }
  .preference__item h3 {
    font-size: 15px;
  }
  .consultation__title {
    font-size: 25px;
  }
  .carousel .tns-item {
    width: 550px;
  }
  .carousel .tns-item img {
    width: 100%;
  }
  .carousel .prev {
    left: 30%;
  }
  .carousel .next {
    right: 30%;
  }
}
@media (max-width: 575px) {
  .btn {
    width: 30vw;
    font-size: 10px;
  }
  .btn_promo {
    width: 60vw;
    font-size: 12px;
  }
  .btn_consultation {
    width: 270px;
    font-size: 12px;
  }
  .promo__item {
    font-size: 10px;
  }
  .promo__item img {
    width: 27vw;
  }
  .promo__item span {
    font-size: 9px;
  }
  .promo__header {
    font-size: 25px;
  }
  .promo__subheader {
    font-size: 14px;
  }
  .promo__icons {
    width: 90px;
    font-size: 10px;
  }
  .preference__title {
    font-size: 25px;
  }
  .preference__wrapper {
    flex-direction: column;
    width: 90vw;
  }
  .preference__item {
    width: 100%;
    font-size: 13px;
    align-items: center;
  }
  .consultation__title {
    font-size: 20px;
  }
  .consultation__description {
    font-size: 14px;
    margin-top: 30px;
  }
  .consultation__form {
    width: 270px;
  }
}
